*, :before, :after {
  box-sizing: border-box;
}

body {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

ol {
  margin: 0;
}

a, a:link, a:visited, a:hover {
  text-decoration: none;
}

section, main {
  display: block;
}

h2, p {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
}

img {
  vertical-align: top;
  max-width: 100%;
  height: auto;
}

.page {
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.15;
}

.people__list {
  counter-reset: myCounter;
  grid-template-columns: repeat(2, 220px);
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: grid;
}

@media (width >= 1000px) {
  .people__list {
    grid-template-columns: repeat(4, 220px);
  }
}

.people__item {
  justify-content: center;
  align-items: center;
  width: 220px;
  list-style-type: none;
  display: flex;
  position: relative;
}

.people__item:before {
  counter-increment: myCounter;
  content: counter(myCounter);
  color: #8b0000;
  z-index: 10;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: inline-flex;
  position: absolute;
  top: 110px;
  right: 20px;
  transform: translateY(-50%);
}

.person {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 8px;
  width: 220px;
  padding: 10px;
  transition: all .3s;
  display: flex;
}

.person:hover {
  background-color: #eee;
  border-radius: 10px;
  transform: scale(1.1);
}

.person:hover .person__details {
  opacity: 1;
}

.person__image {
  aspect-ratio: 1;
  border-radius: 50%;
  max-width: 200px;
  overflow: hidden;
}

.person__image img {
  object-fit: cover;
  width: 100%;
}

.person__name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
}

.person__details {
  opacity: 0;
}
